// 引入 dayjs
import dayjs from 'dayjs'

// 加载中文语言包
// 如果要引用node_modules里的第三方依赖, 禁止使用常规依赖(相对路径和绝对路径)
import 'dayjs//locale/zh-cn'

// 导入处理相对时间的插件
import relativeTime from 'dayjs/plugin/relativeTime.js'

//  引入Vue
import Vue from 'vue'

// dayjs的默认语言是英文, 讲其配置成中文
dayjs.locale('zh-cn')

/*
  全局过滤器的创建
  Vue.filter('过滤器名', value(组件传递过来的参数) =>  {
    return 通过过滤器的业务逻辑处理后的数据结果
  })

  局部过滤器
  export default {
    filters: {
      '自定义过滤器名称' (value(组件传递过来的参数)) {
        return 通过过滤器的业务逻辑处理后的数据结果
      }
    }
  }
*/

// 配置使用处理相对时间的插件
dayjs.extend(relativeTime)

Vue.filter('relativeTime', value => {
  // 通过dayjs().to() 来展示相对时间(查询dayjs文档可以知道这个)
  return dayjs().to(dayjs(value)) // xx年前
  // return dayjs(value).to(dayjs())  // xx年内
})

// 全局过滤器需要在main.js中引入
